/**
 * Selectric
 */
.selectric-wrapper {
  position: relative;
  cursor: pointer;
}

.selectric-responsive {
  width: 100%;
}

.selectric {
  border-radius: 0px;
  background: var(--wp--preset--color--white);
  position: relative;
  overflow: hidden;
  border: 1px solid var(--wp--preset--color--light-periwinkle);
}

.selectric .label {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  margin: 0 38px 0 15px;
  font-size: 12px;
  line-height: 44px;
  color: var(--wp--preset--color--arsenic);
  height: 45px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: "Montserrat", "sans-serif";
  font-weight: 600;
  text-transform: uppercase;
  text-align: left;
}

.selectric .button {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 44px;
  height: 44px;
  line-height: 44px;
  background-color: var(--wp--preset--color--white);
  color: var(--wp--preset--color--secondary);
  text-align: center;
  font: 0/0 a;
  *font: 20px/44px Lucida Sans Unicode, Arial Unicode MS, Arial;
}

.selectric .button:after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-top-color: var(--wp--preset--color--secondary);
  border-bottom: none;
}

.selectric-focus .selectric {
  border-color: var(--wp--preset--color--gray);
}

.selectric-hover .selectric {
  border-color: var(--wp--preset--color--chinese-gray);
}

.selectric-hover .selectric .button {
  color: var(--wp--preset--color--quick-silver);
}

.selectric-hover .selectric .button:after {
  border-top-color: var(--wp--preset--color--quick-silver);
}

.selectric-open {
  z-index: 9999;
}

.selectric-open .selectric {
  border-color: var(--wp--preset--color--gray);
}

.selectric-open .selectric-items {
  display: block;
}

.selectric-disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
}

.selectric-hide-select select {
  position: absolute;
  left: -100%;
}

.selectric-hide-select.selectric-is-native {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.selectric-hide-select.selectric-is-native select {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border: none;
  z-index: 1;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  opacity: 0;
}

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important;
}

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important;
}

/* Items box */
.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: var(--wp--preset--color--cultured);
  border: 1px solid var(--wp--preset--color--american-silver);
  z-index: -1;
  -webkit-box-shadow: 0 0 10px -6px;
  box-shadow: 0 0 10px -6px;
  width: 100% !important;
}

.selectric-items .selectric-scroll {
  height: 100%;
  overflow: auto;
}

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%;
}

.selectric-items ul,
.selectric-items li {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 12px;
  line-height: 20px;
  min-height: 20px;
}

.selectric-items li {
  display: block;
  padding: 10px;
  color: var(--wp--preset--color--granite-gray);
  cursor: pointer;
  font-family: "Montserrat", "sans-serif";
  font-weight: 600;
}

.selectric-items li.selected {
  background: var(--wp--preset--color--chinese-white);
  color: var(--wp--preset--color--outer-space);
}

.selectric-items li.highlighted {
  background: var(--wp--preset--color--american-silver);
  color: var(--wp--preset--color--outer-space);
}

.selectric-items li:hover {
  background: var(--wp--preset--color--american-silver);
  color: var(--wp--preset--color--outer-space);
}

.selectric-items .disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default !important;
  background: none !important;
  color: var(--wp--preset--color--granite-gray) !important;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.selectric-items .selectric-group .selectric-group-label {
  font-weight: bold;
  padding-left: 10px;
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: none;
  color: var(--wp--preset--color--outer-space);
}

.selectric-items .selectric-group.disabled li {
  filter: alpha(opacity=100);
  opacity: 1;
}

.selectric-items .selectric-group li {
  padding-left: 25px;
}

.selectric-items li.highlighted {
  background: var(--wp--preset--color--senary);
  color: var(--wp--preset--color--white);
}

.selectric-items li:hover {
  background: var(--wp--preset--color--senary);
  color: var(--wp--preset--color--white);
}

@media (min-width: 1200px) {
  .selectric .label {
    height: 54px;
    line-height: 54px;
    font-size: 14px;
  }
  .selectric .button {
    height: 54px;
  }
}
