.banner-item {
  /* min-height: 611px; */
  height: auto;
}
.banner-item .slick-list {
  height: 100%;
}
.banner-item .slick-slider div {
  height: 100%;
}
.banner-item .slick-dots {
  list-style-type: none;
}
.banner-item .slick-dots li {
  margin: 0 0.25rem;
}
.banner-item .slick-dots button {
  display: block;
  width: 12px;
  height: 12px;
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0.65);
  text-indent: -9999px;
}
.banner-item li.slick-active button {
  background-color: var(--wp--preset--color--white);
}
.banner-item .slick-arrow {
  font-size: 0;
  line-height: 0px;
  border: none;
  background: none;
  cursor: pointer;
  position: absolute;
  left: auto;
  top: auto;
  right: auto;
  z-index: 99;
  margin-top: auto;
}
.banner-item .slick-arrow::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "Icomoon", sans-serif !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.banner-item .slick-arrow:before {
  content: "\e912";
}
.banner-item .slick-arrow:before {
  display: block;
  font-size: 34px;
  color: rgba(255, 255, 255, 0.65);
  background: none;
  width: auto;
  height: auto;
}
.banner-item .slick-prev:before {
  -webkit-transform: rotate(-180deg);
  -ms-transform: rotate(-180deg);
  transform: rotate(-180deg);
  position: relative;
}
.banner-item .slick-next:before {
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
  position: relative;
}

.banner-img img,
.mobile-banner-img img {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.mobile-banner-img {
  height: 229px;
}

.banner-overlay {
  /* background-color: rgba(28, 32, 42, .2); */
  color: var(--wp--preset--color--white);
  text-align: center;
  top: 0;
}
.banner-overlay-cont {
  text-align: center;
}

.banner-txt {
  padding-top: 160px;
}

.banner-pagination {
  display: none;
  position: absolute;
}

.banner-form {
  position: relative;
  margin-top: 0;
  padding: 30px 20px;
  background: url(https://res.cloudinary.com/ventureoutdoors/image/upload/q_auto,f_auto,c_scale,h_361/background-images/bg-guide-blue.png) 0 0 repeat;
  color: var(--wp--preset--color--white);
  height: 361px;
}

.banner-form__content {
  -webkit-transition: opacity 0.2s linear;
  -o-transition: opacity 0.2s linear;
  transition: opacity 0.2s linear;
}

.banner-form.is-loading .banner-form__content {
  opacity: 0.3;
}

.banner-form__content .h4,
.banner-form__content p {
  text-align: center;
  text-shadow: 0px 0px 10px rgb(0, 0, 0);
}

.banner-form.is-loading:before {
  content: "";
  position: absolute;
}

.banner-form.is-loading:before {
  top: 50%;
  left: 50%;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 3px solid var(--wp--preset--color--white);
  border-top-color: transparent;
  border-bottom-color: transparent;
  z-index: 1000;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-animation: isLoading 0.75s linear infinite;
  animation: isLoading 0.75s linear infinite;
}

@-webkit-keyframes isLoading {
  0% {
    -webkit-transform: translate(-50%, -50%) rotateZ(0);
    transform: translate(-50%, -50%) rotateZ(0);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotateZ(360deg);
    transform: translate(-50%, -50%) rotateZ(360deg);
  }
}
@keyframes isLoading {
  0% {
    -webkit-transform: translate(-50%, -50%) rotateZ(0);
    transform: translate(-50%, -50%) rotateZ(0);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) rotateZ(360deg);
    transform: translate(-50%, -50%) rotateZ(360deg);
  }
}
