/**
* Sliders/Carousels
* Slick Slider - https://kenwheeler.github.io/slick/
*/
/* General styling */
.slick-slider {
  position: relative;
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: "";
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir=rtl] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick-slide,
.slick-slide:focus * {
  outline: none !important;
}

.slick-initialized .slick-slide {
  float: none;
  display: inline-block;
  vertical-align: middle;
}

.slick-arrow:focus {
  outline: none !important;
}

/* Preload affect */
.slick-slider .slide {
  display: none;
}

.slick-slider .slide:first-child {
  display: block;
}

.slick-slider.slick-initialized .slide {
  display: block;
}

.js-slider-has-preloader {
  /* Add this class to your slider */
  min-height: 50px;
  position: relative;
}

.js-slider-has-preloader:before {
  content: url("slick-carousel/slick/ajax-loader.gif");
  /* Create and upload a loading gif to your image directory */
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  /* Update width based on gif size */
  height: 50px;
  /* Update height based on gif size */
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.js-slider-has-preloader-init:before {
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
}

/* Custom styling per slider/carousel */
.slick-arrow {
  font-size: 0;
  line-height: 0px;
  border: none;
  background: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  /* left: 30px; */
  z-index: 99;
  /* margin-top: -24px; */
}

.slick-arrow:before {
  display: block;
  font-size: 24px;
  line-height: 24px;
  color: var(--wp--preset--color--white);
  background: var(--wp--preset--color--cyan-cornflower-blue);
  width: 24px;
}

.slick-prev:before {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

.slick-next:before {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}

.slick-next {
  left: auto;
  right: 30px;
}

